<template>
  <div class="myVideo">
    <div class="test_two_box">
      <video id="myVideo" class="video-js">
        <source :src="videoUrl" type="video/mp4" />
      </video>
      <i class="fa fa-window-close-o" @click="close" aria-hidden="true"></i>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      videoUrl: ''
    }
  },
  mounted() {
    this.videoUrl = this.$store.state.videoUrl
    setTimeout(() => {
      this.initVideo()
    }, 1000)
  },
  methods: {
    initVideo() {
      this.$video(myVideo, {
        //autoplayPlayer API
        controls: true,
        //,muted:
        autoplay: 'muted',
        //<video>
        preload: 'auto',
        //
        width: '1200px',
        //
        height: '600px'
      })
    },
    close() {
      // alert('close')
      this.$emit('closeVideo', true)
    }
  }
}
</script>

<style lang="less" scoped>
.myVideo {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}
.test_two_box {
  position: relative;
  i {
    position: absolute;
    top: 15px;
    right: 15px;
    transform: scale(1.8);
    color: #fff;
  }
}
</style>
