import swal from 'sweetalert2'

export default {
  install: Vue => {
    // sweetalert2 
    swal.setDefaults({
      type: 'warning',
      // showCancelButton: true,
      confirmButtonColor: 'rgb(140,212,245)',
      cancelButtonColor: 'rgb(193,193,193)',
      buttons: ['', ''],
    })

    // 
    Vue.swal = swal
    // 
    Vue.prototype.$swal = swal
  },
}
